// 
function keyDownHandler(e, binding) {
    const { value } = binding;
    if(!value) {
        return
    }
    const reg = new RegExp(value)
    if (reg.test(e.key)) {
        e.stopPropagation();
        e.preventDefault();
        return;
    }
}

// 
// v-disable-input-char="/'/"
export default {
    bind(el, binding) {
        let inputElement = el;
        // inputinput
        if (el.tagName !== "INPUT") {
            inputElement = el.querySelector("input");
        }

        // 
        el.__vueDisableInputChar__ = function(e) {
            keyDownHandler(e, binding)
        }
        inputElement.addEventListener('keydown', el.__vueDisableInputChar__)
    },

    unbind(el) {
        inputElement.removeEventListener('keydown', el.__vueDisableInputChar__)
    }
};



